@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600&display=swap);
body{font-family:'Quicksand', sans-serif}.app{margin:0 auto;display:flex;align-items:center;flex-direction:column;padding:0 1em 2em 1em;font-size:16px;width:100%}@media screen and (min-width: 800px){.app{width:80%;max-width:1000px}}h1{font-size:30px;font-weight:600;margin-top:0}h5{font-size:18px;line-height:1.5}.logo{width:200px}.main{width:100%}.progress-container{position:relative;width:100%;margin:1em 0}.progress-background{width:100%;border-radius:4px;height:4px;background-color:#eee}.progress-value{position:absolute;border-radius:4px;height:4px;background-color:#febd59;transition:width 0.2s ease-out}.btn-nav{background-color:#a28bb8;border-radius:5px;border-width:1px;color:lightblue}.btn-nav:hover{background-color:#854db9}div.header{color:#a28bb8}div.container{width:100%;background-color:lightblue;padding-bottom:1em;margin-bottom:2em}

